.Course-Contact__Login-error div {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-flow: column;
  }
  .Course-Contact__Login-error div a {
    width: 100%;
    max-width: 240px;
    height: 54px;
    padding: 8px;
    font-size: 0.8rem;
    font-weight: 900;
    color: #ff4655;
    text-align: center;
    text-transform: uppercase;
    text-decoration: none;
    position: relative;
    margin: 10px 0;
  }
  .Course-Contact__Login-error div a.white:hover > p {
    color: #ece8e1;
  }
  .Course-Contact__Login-error div a.white > p {
    background: #ece8e1;
    color: #0f1923;
  }
  .Course-Contact__Login-error div a.white > p span.base {
    border: 1px solid transparent;
  }
  .Course-Contact__Login-error div a.transparent:hover > p {
    color: #ece8e1;
  }
  .Course-Contact__Login-error div a.transparent:hover > p span.text {
    box-shadow: 0 0 0 1px #ece8e1;
  }
  .Course-Contact__Login-error div a.transparent > p {
    background: #0f1923;
    color: #ece8e1;
  }
  .Course-Contact__Login-error div a.transparent > p span.base {
    border: 1px solid #ece8e1;
  }


  .Course-Contact__Login-error div a p {
    margin: 0;
    height: 54px;
    line-height: 54px;
    box-sizing: border-box;
    z-index: 1;
    left: 0;
    width: 100%;
    position: relative;
    overflow: hidden;
  }
  .Course-Contact__Login-error div a p span.base {
    box-sizing: border-box;
    position: absolute;
    z-index: 2;
    width: 100%;
    height: 100%;
    left: 0;
    border: 1px solid #ff4655;
  }
  
  .Course-Contact__Login-error div a p span.bg {
    left: -5%;
    position: absolute;
    background: #ff4655;
    width: 0;
    height: 100%;
    z-index: 3;
    transition: 0.3s ease-out all;
    transform: skewX(-10deg);
  }
  .Course-Contact__Login-error div a p span.text {
    z-index: 4;
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
  }
  .Course-Contact__Login-error div a p span.text:after {
    content: "";
    width: 4px;
    height: 4px;
    right: 0;
    bottom: 0;
    background: #0f1923;
    position: absolute;
    transition: 0.3s ease-out all;
    z-index: 5;
  }
  .Course-Contact__Login-error div a:hover {
    color: #ece8e1;
  }
  .Course-Contact__Login-error div a:hover span.bg {
    width: 110%;
  }
  .Course-Contact__Login-error div a:hover span.text:after {
    background: #ece8e1;
  }
  

